import React from 'react'
import dubai_1 from '../images/dubai_1.jpg';

const PackagesItems = () => {
    return (
        <div className="col sameHeightPackages" data-bs-toggle="modal" data-bs-target="#Modal2">
            <div className="card bg-dark text-white h-100 cardHover">
                <img src={dubai_1} className="card-img packagesCardItems" alt="..." />
                <div className="card-img-overlay gradientCard">
                    <div className='packagesDetails'>
                        <h5 className="card-title">Fortune Atrium / Palm Beach</h5>
                        <h6 className="card-text">5 Day 4 Night</h6>
                        <p className="card-text">Click here for more details</p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default PackagesItems